import React, { Component } from 'react';
import { TableData } from 'components/common/index';
import { moneyToString } from 'utils/formatUtil';

export default class TableSubjectData extends Component {
  state = {
    reload: 0,
  };

  columns = [
    {
      title: 'Ký hiệu',
      dataIndex: 'kyhieu',
      key: 'kyhieu',
    },
    {
      title: 'Tên môn',
      dataIndex: 'tenmon',
      key: 'tenmon',
    },
    {
      title: 'Số tín chỉ',
      dataIndex: 'sotinchi',
      key: 'sotinchi',
    },
    {
      title: 'Tên lớp học lại',
      dataIndex: 'tenlophoclai',
      key: 'tenlophoclai',
    },
    {
      title: 'Số SV',
      dataIndex: 'sosinhvien',
      key: 'sosinhvien',
    },
    {
      title: 'Đơn giá',
      dataIndex: 'dongia',
      key: 'dongia',
      render: text => moneyToString(text),
    },
    {
      title: 'Thành tiền',
      dataIndex: 'thanhtien',
      key: 'thanhtien',
      render: (text, record) => moneyToString((record.dongia * record.sosinhvien)),
    },
  ];

  render() {
    const { service, rowSelection, paramSearch } = this.props;
    return (
      <TableData
        rowSelection={rowSelection}
        reload={this.state.reload}
        paramSearch={paramSearch}
        columns={this.columns}
        service={service}
        rowKey="index"
      />
    )
  }

}


