import React, { Component } from 'react';
import {
  Row,
  Col,
  Form,
  Checkbox,
  Radio,
  InputNumber,
} from 'antd';
import {
  moneyToString,
  stringToMoney,
} from 'utils/formatUtil';
import { udpateQueryParams, getQueryParams } from 'utils/urlUtil';
import { SelectAjax } from 'components/common/index';
import { BtnSearch, BtnClear } from 'components/common/button';
import FilterPathEducate from 'components/common/input/FilterPathEducate/index';

@Form.create({ name: 'SearchBar' })
class SearchBar extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;

    form.setFieldsValue(getQueryParams());
  };

  getSemester = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Học kỳ 1'
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getDotthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1'
        },
        {
          id: '2',
          label: '2'
        },
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getKhoanthu = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Khoản thu 1'
        },
        {
          id: '2',
          label: 'Khoản thu 2'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getNamhoc = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '2018-2019'
        },
        {
          id: '2',
          label: '2019-2020'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getDot = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: '1'
        },
        {
          id: '2',
          label: '2'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  getLop = () => {
    const data = {
      currentPage: 1,
      isEmpty: false,
      isNotEmpty: true,
      items: [
        {
          id: '1',
          label: 'Lớp A'
        },
        {
          id: '2',
          label: 'Lớp B'
        }
      ],
      resultsPerPage: 10,
      totalPages: 1,
      totalResults: 6,
    };
    const res = {
      data,
    };
    return res;
  }

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      udpateQueryParams(values);
    });
  };

  handleReset = e => {
    this.props.form.resetFields();
    this.handleSearch(e);
  };

  formItemLayout = 6;

  render() {
    const {
      getFieldDecorator,
      setFieldsValue,
      getFieldValue,
    } = this.props.form;
    const {
      onChangeMasterPrice,
      masterPrice,
    } = this.props;
    const chontatca = getFieldValue('chontatca');
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
    };

    return (
      <Form className="form-inline-label" layout="vertical" onSubmit={this.handleSearch}>
        <Row>
          <FilterPathEducate form={this.props.form} classes />
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Lớp">
              {getFieldDecorator('lop', {})(<SelectAjax service={this.getLop} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Năm học">
              {getFieldDecorator('namhoc', {})(<SelectAjax service={this.getNamhoc} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Học kì">
              {getFieldDecorator('hocki', {})(<SelectAjax service={this.getSemester} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('chontatca', {})(<Checkbox checked={chontatca}>Chọn tất cả</Checkbox>)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Khoản thu">
              {getFieldDecorator('khoanthu', {})(<SelectAjax service={this.getKhoanthu} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Đợt thu">
              {getFieldDecorator('dotthu', {})(<SelectAjax service={this.getDotthu} setFieldsValue={setFieldsValue} />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item {...formItemLayout} label="Nhập đơn giá môn">
              <InputNumber
                defaultValue={masterPrice}
                formatter={value => moneyToString(value)}
                parser={value => stringToMoney(value)}
                onChange={onChangeMasterPrice}
              />
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={6}>
            <b style={{ color: 'red' }}>Lưu ý: Dữ liệu học lại, thi lại lấy từ phòng đào tạo</b>
          </Col>
          <Col span={9}>
            <Form.Item {...formItemLayout}>
              {getFieldDecorator('loaidanhsach', {})(
                <Radio.Group setFieldsValue={setFieldsValue}>
                  <Radio value='dshoclai'>Lập DS học lại</Radio>
                  <Radio value='dsthilai'>Lập DS thi lại</Radio>
                  <Radio value='dsthilaicaithien'>Lập DS thi lại cải thiện</Radio>
                </Radio.Group>
              )}
            </Form.Item>
          </Col>
          <Col span={3}>
            Tổng số: <b style={{ color: 'red' }}>3</b>
          </Col>
          <Col span={6} style={{ float: 'right' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" title='Tìm kiếm' />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form >
    );
  }
}

export default SearchBar;



